@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    body {
        font-family: 'Poppins', 'sans-serif';
    }

    .social-links {
        opacity: 100 !important;
        transition: opacity 1s ease !important;
    }

    .fade-out {
        opacity: 0;
        transition: opacity 1s ease;
    }

    .fade-in {
        transition: opacity 2s ease;
    }

}